.PropertiesView {
  display: relative;
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  border-left: 1px solid black;
}

.PropertiesView.expanded {
  -ms-flex-preferred-size: 20em;
      flex-basis: 20em;
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  background-color: #ddddee;
}

.PropertiesView.collapsed {
  -ms-flex-preferred-size: 1em;
      flex-basis: 1em;
  background-color: black;
}

.PropertiesView.expanded .PropertiesView-expandButton {
  display: none;
}

.PropertiesView-expandButton {
  text-align: center;
  width: 1em;
  height: 100%;
  cursor: pointer;
  color: darkgray;
}

.PropertiesView-expandButton:hover {
  background-color: #222222;
  color: white;
}

.PropertiesView.collapsed .PropertiesView-contents {
  display: none;
}



