.NodeView {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: visible;
}

.NodeView-content {
  position: absolute;
  background-color: #666666;
  color: white;
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
}

.NodeView.locked .NodeView-content {
  background-color: black;
}


