.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: red;
}

.App-loading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #333355;
  color: white;
  text-align: center;
  line-height: 100%;
  vertical-align: middle;
  cursor: wait;
}



